.game-block {
  font-family: 'Raleway', sans-serif !important;
  font-size: 17px !important;
  line-height: 30px !important;
  color: #5a5858 !important;

  button::before {
    content: '';
  }

  a.cmn-btn {
    background-color: #ffc028;
    color: #fff;
    text-transform: uppercase;
    font-weight: bold;
    border: none;
    padding: 12px;
  }
  a.cmn-btn:hover {
    background-color: #000;
    color: #fff;
  }

  button.cmn-btn {
    background-color: #ffc028;
    color: #fff;
    text-transform: uppercase;
    font-weight: bold;
    border: none;
    padding: 12px;
    width: 100%;
    margin-top: 14px
  }
  button.cmn-btn:hover {
    background-color: #000;
    color: #fff;
  }

  button.cmn-btn2 {
    background-color: #a11b1f;
    color: #fff;
    border-radius: 0;
    font-size: 14px;
    line-height: normal;
    padding: 10px 30px;
    margin-top: 20px;
  }
  button.cmn-btn2:hover {
    color: #fff;
  }
  button.cmn-btn2:focus, button.cmn-btn2:active {
    color: #fff;
  }

  .clearfix:before, .clearfix:after {
    content: "";
    display: table;
  }
  .clearfix:after {
    clear: both;
  }
  .clearfix {
    zoom: 1;
  }

  /*Login page css start*/
  *, h1, h2 {
    padding: 0;
    margin: 0;
  }

  .form-control {
    border-radius: 0px;
  }
  .btn {
    border-radius: 0px;
  }

  .login-page {
    background: url(../images/banner.png) no-repeat center top;
    width: 100%;
    height: 560px;
    background-size: cover;
    margin-top: 0px;
    position: relative;
  }
  .login-box {
    margin: 0 auto;
    padding: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
  }
  /*Login page css end*/

  /*Home page css start*/

  .draganddrop-outer {
    display: inline-block;
    float: left;
    width: 100%;
    padding: 2% 0;
    width: 100%;
    text-align: center;
    padding-bottom: 11px;
  }
  #header {
    background: #d7d7d7;
    display: block;
    min-height: 72px;
  }
  .game-zone { /*width: 1040px*/
    background: #f6f6f4;
    padding: 20px 7px;
    box-sizing: border-box;
    float: left;
  }
  .game-zone table {
    text-align: center;
  }
  .game-zone table th {
    text-align: center;
    background: #e3e2df;
    color: #000;
    text-transform: uppercase;
    font-size: 17px;
    border-right: 5px solid #f6f6f4; /*padding: 5px 0;*/
    width: 225px;
  }
  .game-zone table td {
    font-size: 12px;
    text-transform: uppercase;
    color: #51524e;
    font-weight: bold;
    min-width: 50px;
  }
  .que-zone {
    text-align: left;
    font-size: 16px;
    line-height: 23px;
  }
  /*.que-zone p{font-size: 14px;}*/

  table.btm-table th {
    border-radius: 10px; /*padding: 10px 10px;*/
  }

  .box {
    border-radius: 10px;
    padding: 0 8px;
    width: 98px;
    height: 98px;
    color: #fff;
    border: 2px solid #000;
    text-align: left;
    /*-webkit-transition: all 0.5s linear;
          transition: all 0.5s linear;
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);*/

  }
  .box label.title {
    font-weight: 600;
    font-size: 13px;
    margin: 0;
    padding: 9px 0;
    line-height: 16px;
    font-family: arial;
    word-wrap: break-word;
  }
  .box p.desc {
    font-size: 11px;
    line-height: 12px;
    margin: 0;
    padding: 0;
  }
  .box span.points {
    margin: 0;
    padding: 3px 6px;
    font-size: 11px;
    line-height: 12px;
    background: #000;
    position: absolute;
    bottom: 3px;
    left: 8px;
    border-radius: 10px;
  }

  .box-bottom {
    border-radius: 10px;
    margin-top: 5px;
    margin-bottom: 5px;
    padding: 8px;
    width: 98px;
    height: 104px;
    color: #fff;
    position: relative;
    border-radius: 10px;
    margin-top: 0;
    margin-right: 0;
    margin-bottom: 0;
    padding: 8px;
    width: 204px;
    height: 60px;
    color: #fff; /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#ffb910+1,ffb910+50,ffab0e+52,ffab0e+100 */
