    background: -moz-linear-gradient(-45deg, #ffb910 1%, #ffb910 50%, #ffab0e 52%, #ffab0e 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(-45deg, #ffb910 1%, #ffb910 50%, #ffab0e 52%, #ffab0e 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(135deg, #ffb910 1%, #ffb910 50%, #ffab0e 52%, #ffab0e 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
