  .box-bottom label.title {
    font-weight: 700;
    font-size: 13px;
    margin: 0;
    padding: 0;
    line-height: 15px;
    font-family: arial;
    text-align: left;
    text-transform: none;
    display: inherit;
  }
  .box-bottom p.desc {
    font-size: 11px;
    line-height: 13px;
    margin: 0;
    padding: 0;
    text-align: left;
    text-transform: none;
  }
  .box-bottom span.star {
    position: absolute;
    right: 5px;
    top: 22px;
  }

  .box-contianer {
    display: block;
    float: left;
    margin-top: 5px;
    margin-left: 5px;
    background: #eee1e1;
    border-radius: 10px; /*margin-bottom: 5px;*/
  }

