  .cross {
    display: block;
    margin: auto;
    width: 98px;
    height: 98px;
    position: relative;
    border: solid 1px #c8c6c4;
    background: #e3e2df;
    position: relative;
    border-radius: 10px; /*margin-top: 5px; margin-left: 6px; margin-bottom: 5px; */
  }
  .cross:before {
    content: '';
    position: absolute;
    top: 50%;
    left: -16px;
    width: 130px;
    height: 1px;
    background: #c8c6c4;
    transform: rotate(45deg);
  }
  .cross:after {
    content: '';
    position: absolute;
    top: 50%;
    left: -16px;
    width: 130px;
    height: 1px;
    background: #c8c6c4;
    transform: rotate(-45deg);
  }

